import { newSpecPage } from '@stencil/core/testing';
import { PrestoDynamicOverlays } from '../presto-dynamic-overlays';

describe('presto-dynamic-overlays', () => {
  it('renders', async () => {
    const page = await newSpecPage({
      components: [PrestoDynamicOverlays],
      html: `<presto-dynamic-overlays></presto-dynamic-overlays>`,
    });
    expect(page.root).toEqualHtml(`
      <presto-dynamic-overlays>
        <mock:shadow-root></mock:shadow-root>
      </presto-dynamic-overlays>
    `);
  });
});
